import React, {Component} from 'react';
import ViewPortfolio from '../viewing/ViewPortfolio';
import {db} from '../auth/firebase';
import '../../styles/Portfolio.css';

class Portfolio extends Component {

    constructor(props) {
        super(props);
        this.state = {
            portfolio: [],
            skills: [],
        };
    }

    componentDidMount() {
        db.collection("Projects")
        .orderBy("priority", "asc")
        .onSnapshot(querySnapshot => {
            let portfolio = [];
            querySnapshot.forEach(function(doc) {
                let temp = {};
                // fill temp
                temp.id = doc.id;
                temp.title = doc.data().title;
                temp.imageUrl = doc.data().imageUrl;
                temp.url = doc.data().url;
                temp.description = doc.data().description;
                // add temp to portfolio
                portfolio.push(temp);
            });
            this.setState({portfolio});
        })

        db.collection("Skills")
        .orderBy("priority", "asc")
        .onSnapshot((querySnapshot) => {
            let skills = [];
            querySnapshot.forEach(function(doc) {
                let temp = {};
                // fill temp
                temp.id = doc.id;
                temp.title = doc.data().title;
                temp.all = doc.data().list;
                temp.show = doc.data().show;
                // add temp to portfolio
                skills.push(temp);
            });
            this.setState({skills});
        });
    }

    render() {
        return (
            <div className="portfolio-section" id="portfolio-section">
                <div className="wave-background"></div>
                <ViewPortfolio portfolio={this.state.portfolio} skills={this.state.skills}/>
            </div>
        );
    }
}

export default Portfolio